:root {
  --h4-p-color-tomato: hsl(4, 100%, 67%);
  --h4-n-dark-slate-gray: hsl(234, 29%, 20%);
  --h4-n-dark-slate-gray-hover: hsl(234, 29%, 30%);
  --h4-n-charcoal-grey: hsl(235, 18%, 26%);
  --h4-n-grey: hsl(231, 7%, 60%);
  --h4-n-white: hsl(0, 0%, 100%);
  --h4-btn-orange: #ff6644;
  --h4-btn-tomato: #ff5379;
  --h4-btn-gradient: linear-gradient(
      90deg,
      var(--h4-btn-tomato),
      var(--h4-btn-orange)
  );
}

.btn-h4-dark {
  --bs-btn-color: var(--h4-n-white);
  --bs-btn-bg: var(--h4-n-dark-slate-gray);
  --bs-btn-border-color: #212529;
  --bs-btn-hover-color: var(--h4-n-white);
  --bs-btn-focus-shadow-rgb: 66, 70, 73;
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: #4d5154;
  --bs-btn-active-border-color: #373b3e;
  --bs-btn-disabled-color: #fff;
  --bs-btn-disabled-bg: #212529;
  --bs-btn-disabled-border-color: #212529;
}

.btn-h4-dark:hover {
  background-image: var(--h4-btn-gradient);
  box-shadow: 0 10px 40px -3px var(--h4-btn-tomato);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  width: 100%;
  margin: 0;
  min-height: 100svh;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
}

.custom-width {
  max-width: 375px;
}

.form__list {
  list-style-image: url(/assets/images/icon-list.svg);
}

#mail_input {
  cursor: pointer;
  border: 2px solid rgba(147, 147, 147, 0.4);
}
#mail_input:hover {
  box-shadow: 3px 4px 15px var(--h4-btn-orange);
}
#mail_input:focus {
  box-shadow: 1px 2px 10px var(--h4-btn-orange);
  background: #fff;
}

.invalid {
  background: #ffe8e6;
  color: var(--h4-btn-orange);
  border: 2px solid var(--h4-btn-tomato) !important;
  opacity: 0.6;
}

@media screen and (min-width: 992px) {
  .custom-width {
    max-width: 1440px;
  }
  .display-2 {
    font-size: 3.5rem;
  }
  .message {
    max-height: 50%;
    border: 10px solid skyblue;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
  }
}
/* Atribution */
.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(245, 46%, 45%);
}